.form__group__checkbox .option{
   background: #EEEEEE;
   height: 100%;

   display: flex;
   align-items: center;
   justify-content: space-evenly;
 
   border-radius: 5px;
   cursor: pointer;
   padding: 10px 20px;
   width: 250px;
   border: 2px solid lightgrey;
   transition: all 0.3s ease;
   white-space: nowrap;
   overflow: hidden;
   text-overflow: ellipsis;
   display: block;

 }
 .form__group__checkbox .option .dot{
   height: 20px;
   width: 20px;
   background: #d9d9d9;
   border-radius: 50%;
   position: relative;
 }
 .form__group__checkbox .option .dot::before{
   position: absolute;
   content: "";
   top: 4px;
   left: 4px;
   width: 12px;
   height: 12px;
   background: #0069d9;
   border-radius: 50%;
   opacity: 0;
   transform: scale(1.5);
   transition: all 0.3s ease;
 }
 .form__group__checkbox{
   input[type="radio"]{
      display: none;
   }
   input[type="checkbox"]{
      display: none;
   }
 }
 
 .form__group__checkbox{
  
   
 }
 .checkbox__input:checked:checked ~ .option,
 .checkbox__input:checked:checked ~ .option{
   border-color: #0069d9;
   background: #0069d9;
 }
 .checkbox__input:checked:checked ~ .option .dot,
 .checkbox__input:checked:checked ~ .option .dot{
   background: #fff;
 }
 .checkbox__input:checked:checked ~ .option .dot::before,
 .checkbox__input:checked:checked ~ .option .dot::before{
   opacity: 1;
   transform: scale(1);
 }
 .wrapper .option span{
   font-size: 20px;
   color: #808080;
 }
 .checkbox__input:checked:checked ~ .option span,
 .checkbox__input:checked:checked ~ .option span{
   color: #fff;
 }

