 $buttonColor: hsl(342, 60%, 51%);

 .file-chooser {
   position: relative;
   display: inline-block;
   box-sizing: border-box;
   font-size: 1rem;
   * {
     box-sizing: inherit;
   }
   .file-chooser-input {
     background: white;
     border: 1px solid gray;
     border-right: none;
     border-radius: 2px;
     padding: 5px;
     font-size: inherit;
     cursor: default;
     padding-right: 80px;
     white-space: pre;
     text-overflow: ellipsis;
     width: 100%;
   }
   &:after {
     content: 'Browse';
     position: absolute;
     top: 0;
     right: 0;
     padding: 8px 10px;
     background: $buttonColor;
     border: none;
     border-radius: 0 2px 2px 0;
     color: #FFF;
     font-size: inherit;
     cursor: pointer;
     transition: background .25s;
     &:hover {
       background: darken($buttonColor, 5%);
     }
   }
   
   .file-chooser-clear {
     position: absolute;
     right: 80px;
     background: none;
     border: none;
     padding: 0;
     top: 50%;
     transform: translateY(-50%);
     cursor: pointer;
     opacity: 0;
     transition: color .25s, opacity .25s;
     &:after {
       content: '\00D7';
       font-size: 1.2rem;
     }
     &:hover {
       color: red;
     }
   }
   &:hover .file-chooser-clear {
     opacity: 1;
   }
   .file-chooser-hidden {
     display: none;
   }
 }
