   overflow: hidden;
   z-index: -1;
 }
 .custom-file__label {
   position: relative;
   display: block;
   width: 50%;
   min-width: 335px;
   
   height: 100%;
   padding: 0;
   background: #ffffff;
   border: 1px solid #dfdfdf;
   color: #666666;
   line-height: 45px;
   text-align: center;
   text-transform: none;
   display: flex;
   align-items: center;
   padding-left: 20px;
   cursor: pointer;
   transition: all 0.3s;
 }
 .custom-file__input:valid ~ .custom-file__label {
   border-color: #39B54A;
   background: #39B54A;
   color: #39B54A;
 }
 .custom-file__input:valid ~ .custom-file__label:before {
   content: '\f00c';
   position: absolute;
   top: 0;
   right: 0;
   bottom: 0;
   left: 0;
   margin: auto;
   color: #ffffff;
   font-family: 'FontAwesome';
   line-height: 45px;
 }
 

